import CatInfo from '../Component/CatInfo';
import React, { useState, useEffect, useMemo } from 'react';
import UseCalculate from '../Component/UseCalculate';
import UseCalculateWithMemo from '../Component/UseCalculateWithMemo';

function UseMemoPracticePage() {
	const [count, setCount] = useState(0);
	//    =>   
	const [age, setAge] = useState(1);
	// age   => useMemo    

	function plusCOunt() {
		setCount(count => count + 1);
	}

	function plusAge() {
		setAge(age => age + 1);
	}

	const [num, setNum] = useState(0);
	const [isFront, setIsFront] = useState(true);

	// const developer = isFront ? '' : '';
	const developer = {
		field: isFront ? '' : '',
	};

	useEffect(() => {
		console.log('render3');
	}, [isFront]);

	// const developer = useMemo(() => {
	// 	return {
	// 		field: isFront ? '' : '',
	// 	};
	// }, [isFront]);

	const [numA, setNumA] = useState(0);
	const [numB, setNumB] = useState(0);

	return (
		<>
			<div style={{ padding: '40px' }}>
				<CatInfo name="  " age={age} />
				{console.log('render1')}
				<button onClick={plusCOunt} style={{ marginTop: '20px' }}>
					: {count}
				</button>
				<br />
				<button onClick={plusAge} style={{ marginTop: '20px' }}>
					: {age}
				</button>
			</div>
			<hr />
			<div style={{ padding: '40px' }}>
				<h2>   ? </h2>
				<input
					type="number"
					value={num}
					onChange={e => {
						setNum(e.target.value);
					}}
				/>

				<h2>   ? </h2>
				{/* <p> {developer}</p> */}
				<p> {developer.field} </p>

				<button onClick={() => setIsFront(!isFront)}> ..  !!!! </button>
			</div>

			<div style={{ padding: '40px' }}>
				<input
					type="number"
					value={numA}
					onChange={e => {
						setNumA(e.target.value);
					}}
				/>
				<input
					type="number"
					value={numB}
					onChange={e => {
						setNumB(e.target.value);
					}}
				/>
				<hr />
				<p>useEffect  </p>
				<UseCalculate numA={numA} numB={numB} />
				<hr />
				<p>useMemo </p>
				<UseCalculateWithMemo numA={numA} numB={numB} />
			</div>
		</>
	);
}
export default UseMemoPracticePage;
