//  useState    ,    Hooks  .


import { useState } from 'react';
import React from 'react';

function Counter() {
    const [number, setNumber] = useState(0);

    const increase = () =>  {
        console.log("number = ", number);
        // setNumber(number - 1);
        setNumber(prevNumber => prevNumber + 1);
        //  Setter    ,        ,
        //                 .
        console.log("number = ", number);
    }

    const decrease = () => {
        console.log("number = ", number);
        setNumber(number - 1);
        console.log("number = ", number);
    }

    const [count, setCount] = useState(0);

    const onClick1 = () => {
        setCount(count+ 1);
        setCount(count+ 1);
        console.dir(count);
    }

    const onClick2 = () => {
        setCount(count=> count+ 1);
        setCount(count=> count+ 1);
        //  setCount     count  count    .
        console.dir(count);
    }

    //       ""  .

    // count+ 1 (  )  count=> count+ 1 (  )   ? 

    //       (useState  state     ) 
    //   . 
    // state    state       
    // useState  state       .
    //  state  (async)  .
    // setState   state   
    //   state  
    //   state     
    //    state  .
    // (promise setTimeout       state  .)
    //  1    state     
    //  setState     .
    //    (count)     .
    //          .

    // setState()          . ()
    //            setState()     
    //            .
    //              .

    return (
        <div>
            <h1>{number}</h1>
            <button onClick={increase}>+1</button>
            <button onClick={decrease}>-1</button>
            <h1>{count}</h1>
            <button onClick={onClick1}> count + 1  </button>
            <button onClick={onClick2}> count = count + 1  </button>
        </div>
    );
}

export default Counter;