import React from 'react';

function ListExample (){
    const priceList = [1000, 2000, 3000, 4000];
    const prices = priceList.map( (price) => (<div> : {price} </div>) )

    return(
        <div>
            <h1>List Example</h1>

            <label> </label>
            {prices}
        </div>
    );
}

export default ListExample;