import React from 'react';
import { useDispatch } from 'react-redux';
import { login, logout } from '../Redux/User';

function Login() {
	const dispatch = useDispatch();
	// action   ,   

	return (
		<div>
			<button
				onClick={() => {
					dispatch(login({ name: '', age: 25, email: 'zwon2056@gmail.com' }));
				}}
				//    .
				// => login 
				// => login User "  action     " .
			>
				Login
			</button>
			<button
				onClick={() => {
					dispatch(logout());
				}}
			>
				Logout
			</button>
		</div>
	);
}

export default Login;
